	import Star from "../../components/star/star.vue"
	export default {
		
		data() {
			return {
				carouselList:[],
				hotSuperList:[],
				hotTrailerList:[],
				guessLikeList:[],				
				animationData:{},
				animationDataArr:[
					{},{},{},{},{}
				]
			}
		},
		onUnload(){
			// 页面卸载的时候 清楚动画数据
			this.animationData = {},
			
			this.animationDataArr=[
					{},{},{},{},{}
				]
		},
		onPullDownRefresh(){
			this.refresh()
		},
		onLoad() {

			// 在页面创建的时候 创建一个临时动画对象
			this.animation = uni.createAnimation()

			
						// 请求轮播图  
			var serverUrl = this.serverUrl;
			uni.request({
			    url: serverUrl+'/index/carousel/list',
				method:"POST",
				header:{
					'content-type':'application/x-www-form-urlencoded'
				},
				data:{
					qq:'1764194623'
				},
			    success: (res) => {
			        console.log(res.data);
					// 获取真实数据之前,务必判断状态是否为200
					if(res.data.status == 200){
						var carouselList= res.data.data
						this.carouselList = carouselList
					}
			    }
			});
			
			// 请求热门超英电影详情
			uni.request({
			    url: serverUrl+'/index/movie/hot?type=superhero',
				method:"POST",
				header:{
					'content-type':'application/x-www-form-urlencoded'
				},
				data:{
					qq:'1764194623'
				},
			    success: (res) => {
					if(res.data.status == 200){
						var hotSuperList= res.data.data
						this.hotSuperList = hotSuperList
					}
			    }
			});
			
			// 请求电影预告片 
			uni.request({
			    url: serverUrl+'/index/movie/hot?type=trailer',
				method:"POST",
				header:{
					'content-type':'application/x-www-form-urlencoded'
				},
				data:{
					qq:'1764194623'
				},
			    success: (res) => {
					if(res.data.status == 200){
						var hotTrailerList= res.data.data
						this.hotTrailerList = hotTrailerList
					}
			    }
			});
			
			// 请求猜你喜欢数据
			this.refresh()
			
		},
		methods: {
			// 封装猜你喜欢请求 
			refresh(){
				var serverUrl = this.serverUrl;
				uni.request({
				    url: serverUrl+'/index/guessULike',
					method:"POST",
					header:{
						'content-type':'application/x-www-form-urlencoded'
					},
					data:{
						qq:'1764194623'
					},
				    success: (res) => {
				        console.log(res.data);
						// 获取真实数据之前,判断状态是否为200
						if(res.data.status == 200){
							var guessLikeList= res.data.data
							this.guessLikeList = guessLikeList
						}
				    },
					complete:()=>{
						// 下拉刷新请求到数据后停止
						 uni.stopPullDownRefresh()
					}
				});
			},
			// 点击图片跳转
				showTrailer(e){
					var trailerId = e.currentTarget.dataset.trailerid
					// 跳转前带上id传过去
					uni.navigateTo({
						url: '../movie/movie?trailerId='+trailerId,
					});
			},
			// 点赞动画效果
			praiseMe(e){

				var index = e.currentTarget.dataset.index
				console.log(index)
				// 构建动画数据 通过step表示动画完成
				this.animation.translateY(-60).opacity(1).step({
					duration:500
				})
				this.animationData = this.animation.export()
				this.animationData = this.animation
				this.animationDataArr[index] = this.animationData.export()
				setTimeout(function() {
					this.animation.translateY(0).opacity(0).step({
						duration:0
					});
					this.animationData = this.animation.export()
					this.animationData = this.animation
					this.animationDataArr[index] = this.animationData.export()
				}.bind(this), 600);

		},
		components:{
			Star
		}
	}
}
